import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Card from './components/card';

import cloud from './components/func/getcloud';

const API_KEY = '70d0288ac74c30de975bc2a9e2d41513';
const TRANSLATE = 'trnsl.1.1.20170627T100903Z.3134f17b23b61deb.2a264dc99360357e7cc00655d8e203586280a98f';




class App extends React.Component {
    state = {
        enterCity: true,
        picActive: true,
        weather: {

        }
    }

    data = async (text) => {
        const api_call = await fetch(`http://api.openweathermap.org/data/2.5/weather?&appid=${API_KEY}&lat=${text.coords.latitude}&lon=${text.coords.longitude}`);
        const data = await api_call.json();
        this.translate(data.name)
    }

    location = () => {
        navigator.geolocation.getCurrentPosition(this.data);
    }

    translate = async (text) => {
        const trans_call_en = await fetch(`https://translate.yandex.net/api/v1.5/tr.json/translate?key=${TRANSLATE}&text=${text}&lang=ru-en`);
        const data_en = await trans_call_en.json();
        const en = data_en.text[0];
        const trans_call_ru = await fetch(`https://translate.yandex.net/api/v1.5/tr.json/translate?key=${TRANSLATE}&text=${text}&lang=en-ru`);
        const data_ru = await trans_call_ru.json();
        const ru = data_ru.text[0];
        this.getWeather(en, ru)
    }

    getWeather = async (en, rus) => {
        let pic = '';
        const api_call = await fetch(`http://api.openweathermap.org/data/2.5/weather?q=${en}&appid=${API_KEY}&units=metric`);
        const data = await api_call.json();
        if(api_call.ok == false) {
            this.setState({enterCity: null})
            return false;
        }
        else {
            this.setState({enterCity: false})
        }

        this.setState({
            weather: {
                city: rus,
                temp: Math.round(data.main.temp),
                cloud: cloud(data.weather[0].main)
            }
        })
    }

    notCity = () => {
        this.setState({enterCity: true});
        this.setState({picActive: false});
    }

    componentDidMount() {
        this.location()
    }

    render() {
        return(
            <Card getWeather={this.getWeather} translate={this.translate} enterCity={this.state.enterCity} weather={this.state.weather} notCity={this.notCity} picActive={this.state.picActive}/>
        )
    }
}

ReactDOM.render(<App />, document.getElementById('root'));



