import React from 'react';



export default class Card extends React.Component {

    translate = (e) => {
        e.preventDefault();
        this.props.translate(this.link.value);
    }

    getWeather = (e) => {
        e.preventDefault();
        this.props.translate(this.link.value);
    }

    notCity = () => {
        this.props.notCity();
    }

    render() {
        let ico;
        this.props.picActive === true ? ico = <img src='active.png' alt='' className='resolve_ico'/> : ico = <img src='noactive.png' alt='' className='resolve_ico_grey'/>;
        if (this.props.enterCity === true) {
            return (<div className="container">
                <div className="card-wrapper">
                    <div className="card card-input">
                        {ico}
                        <form>
                            <input type="text" ref={(e) => {
                                    this.link = e
                                }}/>
                            <button onClick={this.getWeather}>Ввод</button>
                        </form>
                    </div>
                </div>
            </div>)
        } else if(this.props.enterCity === false) {
            return (<div className="container">
                <div className="card-wrapper">
                    <div className="card">
                        <div className="not_city" onClick={this.notCity}>Не ваш город?</div>
                        <div className="city">{this.props.weather.city}</div>
                        <div className="date">Сегодня</div>
                        <div className="weather">
                            <div className="sun"><img src={`svg/${this.props.weather.cloud}`} alt=""/></div>
                            <div className="temp">{this.props.weather.temp}°C</div>
                        </div>
                    </div>

                </div>
            </div>)
        } else {
            return (<div className="container">
                <div className="card-wrapper">
                    <div className="card card-input">
                        <div className="err-text">Такого города нет</div>
                        <form action="" className='error'>
                            <input type="text" ref={(e) => {
                                    this.link = e
                                }}/>
                            <button onClick={this.getWeather}>Ввод</button>
                        </form>
                    </div>
                </div>
            </div>)
        }

    }
}



