export default function(arg) {
    switch(arg) {
        case 'Clear':
        return 'weather_clear.svg'
        break;
        case 'Rain':
        return 'weather_rain_n_sleet.svg'
        break;
        case 'Haze':
        return 'weather_fog.svg'
        break;
        case 'Snow':
        return 'weather_snow_n_sleet.svg'
        break;
        default:
        return 'weather_cloudy.svg'
    }
}



